body {
    font-family: sans-serif;
    line-height: 1.5em;
}

H2 {
    margin-left: 2em;
}

body, button, input, select {
    font-size: 18px;
}

pre {
    font-size: 15px;
    color: #005555;
    line-height: 1em;
}

#scopes {
    clear: both;
    position: relative;
}

#scopes canvas {
    display: inline-block;
    float: left;
    width: 100px;
    height: 100px;
}

#red .speakername {
    left: 0px;
}

#orange .speakername {
    left: 100px;
}

#yellow .speakername {
    left: 200px;
}

#green .speakername {
    left: 300px;
}

#cyan .speakername {
    left: 400px;
}

#blue .speakername {
    left: 500px;
}

#purple .speakername {
    left: 600px;
}

.speakername {
    color: #ccc;
}

.speakername.active {
    color: #000;
}

.speakername {
    width: 100px;
    text-align: center;
    position: absolute;
}

#controls {
    clear: both;
}